import {HttpClient} from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Auth - Service, sirve para la identificaciÃ³n y traer datos de la api externa.


*/
@Injectable()
export class AuthServiceProvider {
  
  apiUrl = "http://lamosquitera.org/wp-json/wp/v2/posts";
  apiImg = "http://localhost/apimosquitera/getimages";
  constructor(public http: HttpClient) {
    console.log('Hello QuotesProvider Provider');
  }

  getQuotes(){
    return this.http.get(this.apiUrl);
  }

  getProgramacion(){
    return this.http.get(this.apiImg);
  }


}



