import { Component } from '@angular/core';

@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html',
})
export class WelcomePage {
  audio;
  bandera: boolean;

  constructor() {
    this.audio = new Audio('http://stream.codigosur.org:8000/mosquitera.mp3');
    
  }

  play(){
    this.audio.play();
    this.bandera = true;
    
  }

  stop(){
    
      this.audio.pause();
      this.bandera = false;
  }

}



